import React, { useState } from "react";
import TextField from "@mui/material/TextField";
import Autocomplete from "@mui/material/Autocomplete";
import CircularProgress from "@mui/material/CircularProgress";

import { translate } from "../../utils";

export default function Select({
  fetchAPI,
  value,
  onChange,
  name,
  optionLabel,
  multiple = false,
  label,
  className,
  options: propOptions,
}) {
  const [open, setOpen] = useState(false);
  const [options, setOptions] = useState([]);
  const [loading, setLoading] = useState(false);

  React.useEffect(() => {
    let active = true;
    if (!open) return;
    setLoading(true);
    if (propOptions) {
      setOptions(propOptions);
      setLoading(false);
      return;
    }
    (async () => {
      const res = await fetchAPI();
      if (active) {
        setOptions([...(res || [])]);
        setLoading(false);
      }
    })();
    return () => {
      active = false;
    };
  }, [fetchAPI, propOptions, open]);

  React.useEffect(() => {
    if (!open) {
      setOptions([]);
    }
  }, [open]);

  return (
    <Autocomplete
      id="asynchronous-demo"
      multiple={multiple}
      sx={{ minWidth: 300 }}
      className={className}
      open={open}
      onOpen={() => {
        setOpen(true);
      }}
      onClose={() => {
        setOpen(false);
      }}
      options={options}
      loading={loading}
      name={name}
      value={value}
      onChange={(e, value) => {
        if (multiple) {
          let val = [
            ...(new Map(value.map((item) => [item["id"], item])).values() ||
              []),
          ];
          onChange(val);
        } else {
          onChange(value);
        }
      }}
      placeholder={translate(label)}
      size="small"
      getOptionLabel={(option) =>
        translate(option[optionLabel]) || translate(option)
      }
      renderInput={(params) => (
        <TextField
          {...params}
          label={translate(label)}
          placeholder={translate(label)}
          size="small"
          InputProps={{
            ...params.InputProps,
            endAdornment: (
              <React.Fragment>
                {loading ? (
                  <CircularProgress color="inherit" size={20} />
                ) : null}
                {params.InputProps.endAdornment}
              </React.Fragment>
            ),
          }}
        />
      )}
    />
  );
}
