import React, { useState } from "react";
import Button from "@mui/material/Button";
import { makeStyles } from "@mui/styles";

import Select from "../../components/Select";
import Service from "../../services";
import { fetchProjects } from "../../services/api";
import { translate } from "../../utils";

const useStyles = makeStyles({
  widget: {
    marginLeft: 10,
  },
  root: {
    display: "flex",
    padding: 10,
    alignItems: "center",
    boxShadow: "1px 2px 8px 0 rgba(1,1,1,.75)",
    margin: "14px 16px",
    borderRadius: 5,
  },
});

export default function Toolbar({ addSection = () => {} }) {
  const [section, setSection] = useState({});
  const classes = useStyles();

  const fetchEmployees = React.useCallback(async () => {
    const res = await Service.search("com.axelor.apps.hr.db.Employee", {
      fields: ["fullName", "name", "user", "dailyWorkHours", "product"],
    });
    const employees = res?.data;
    return employees;
  }, []);

  const getProjects = React.useCallback(async () => {
    const projects = await fetchProjects({
      fields: ["name", "membersUserSet", "fullName"],
      data: {
        criteria: [
          {
            fieldName: "projectStatus.isCompleted",
            operator: "=",
            value: false,
          },
        ],
        operator: "and",
      },
    });
    return projects;
  }, []);

  const handleChange = (name, value) => {
    setSection((section) => ({ ...section, [name]: value }));
  };
  return (
    <div className={classes.root}>
      <Select
        name="employee"
        value={section?.employee || ""}
        fetchAPI={fetchEmployees}
        optionLabel="name"
        label={translate("Employee")}
        onChange={(value) => {
          handleChange("employee", value);
        }}
      />
      {section?.employee && (
        <Select
          name="projects"
          multiple={true}
          value={section?.projects || []}
          fetchAPI={getProjects}
          optionLabel="name"
          onChange={(value) => {
            handleChange("projects", value);
          }}
          label={translate("Projects")}
          className={classes.widget}
        />
      )}
      <Button
        variant="outlined"
        style={{ marginLeft: 10, textTransform: "none" }}
        onClick={() => {
          addSection(section);
          setSection({});
        }}
        disabled={
          !section?.employee ||
          section?.projects?.length <= 0 ||
          !section.projects
        }
      >
        {translate("Add")}
      </Button>
    </div>
  );
}
