import Service from "./index";

export const fetchProjects = async (options) => {
  const res = await Service.search(
    "com.axelor.apps.project.db.Project",
    options
  );
  return res?.data;
};

export const updateProject = async (record) => {
  const res = await Service.add("com.axelor.apps.project.db.Project", record);
  return res?.data && res?.data[0];
};
