import React, { useState, useEffect } from "react";
import moment from "moment";

import Toolbar from "../Toolbar";
import AlertBar from "../../components/Alert";
import EventFrom from "../EventForm";
import Service from "../../services";
import { fetchProjects, updateProject } from "../../services/api";
import { translate } from "../../utils";
import { COLORS } from "../colors";
import LocalStorageService from "../../services/LocalStorageService";

import "dhtmlx-scheduler";
import "dhtmlx-scheduler/dhtmlxscheduler.css";
import "./Scheduler.css";

const scheduler = window.scheduler;

const time_step = {
  week_timeline: 10080,
  day_timeline: 1440,
  halfday_timeline: 720,
};

const modeTypeSelect = {
  halfday_timeline: 1,
  day_timeline: 2,
  week_timeline: 3,
  month_timeline: 4,
};

const TYPE = {
  month_timeline: "months",
  day_timeline: "days",
  week_timeline: "weeks",
};

const LABEL = {
  month_timeline: "Month",
  day_timeline: "Day",
  halfday_timeline: "Half day",
  week_timeline: "Week",
};

const DEFAULT_EVENT_COLOR = "#0288d1";

export default function Scheduler({ parameters }) {
  const [mode, setMode] = useState(
    LocalStorageService.getActiveMode() || "day_timeline"
  );
  const [events, setEvents] = useState([]);
  const [open, setOpen] = useState(false);
  const [event, setEvent] = useState(null);
  const [employees, setEmployees] = useState([]);
  const [sections, setSections] = useState([]);
  const [projects, setProjects] = useState([]);
  const [weeklyPlanning, setWeeklyPlanning] = useState([]);
  const [openAlert, setOpenAlert] = useState(false);
  const [message, setMessage] = useState("");
  const [holidays, setHolidays] = useState([]);
  const [leaves, setLeaves] = useState([]);
  const [dates, setDates] = useState({
    date: moment().format("YYYY-MM-DD"),
    toDate: moment().format("YYYY-MM-DD"),
  });
  const { id } = parameters;

  const mapPlanningTimes = React.useCallback((events) => {
    if (!events || !events?.length) return [];
    return events?.map((event) => {
      const {
        date,
        toDate,
        employee,
        project,
        projectTask,
        startOnSelect,
        endOnSelect,
      } = event || {};
      return {
        ...event,
        start_date:
          startOnSelect === 2 ? moment(date).clone().add(12, "hours") : date,
        end_date:
          endOnSelect === 1 ? moment(toDate).clone().add(12, "hours") : toDate,
        text: projectTask?.fullName || project?.fullName,
        section_id: `${employee && employee.id}_${project && project.id}`,
        color: COLORS[project?.id] || DEFAULT_EVENT_COLOR,
      };
    });
  }, []);

  const addProjectLines = React.useCallback(
    async ({
      plannedHours,
      project,
      employee,
      product,
      projectTask,
      description,
      id,
      version,
      start_date,
      end_date,
      startOnSelect,
      endOnSelect,
      hoursPerMode,
    } = {}) => {
      let paylod = {
        date: moment(start_date).format("YYYY-MM-DD"),
        toDate: moment(end_date).format("YYYY-MM-DD"),
        plannedHours,
        startOnSelect,
        endOnSelect,
        employee,
        project: { id: project?.id },
        product: product?.id ? { id: product?.id } : null,
        projectTask: projectTask?.id ? { id: projectTask?.id } : null,
        description,
        hoursPerMode,
        modeTypeSelect: modeTypeSelect[mode],
      };
      if (version >= 0) {
        paylod.version = version;
        paylod.id = id;
      }
      const res = await Service.add(
        "com.axelor.apps.project.db.ProjectPlanningTime",
        paylod
      );
      if (res.status === -1) {
        setEvents((events = []) => events.filter((e) => e.id !== id));
      } else {
        const updatedEvent = (res?.data && res.data[0]) || {};
        setEvents((events = []) =>
          events.map((e) =>
            e.id === id
              ? {
                  ...e,
                  ...updatedEvent,
                  color:
                    COLORS[updatedEvent?.project?.id] || DEFAULT_EVENT_COLOR,
                  text:
                    updatedEvent?.projectTask?.fullName ||
                    updatedEvent?.project?.fullName,
                }
              : e
          )
        );
      }
    },
    [mode]
  );

  const handleClose = () => {
    if (event?.version >= 0) {
      setOpen(false);
      return;
    }
    setEvents((events) => (events || []).filter((e) => e.id !== event?.id));
    setOpen(false);
  };

  const handleDelete = async () => {
    if (event?.version >= 0) {
      const res = await Service.delete(
        "com.axelor.apps.project.db.ProjectPlanningTime",
        event?.id
      );
      if (res.status === 0) {
        setEvents((events) => (events || []).filter((e) => e.id !== event.id));
      }
      setOpen(false);
    } else {
      setEvents((events) => (events || []).filter((e) => e.id !== event?.id));
      setOpen(false);
    }
  };

  const handleOk = () => {
    if (!event?.project) {
      setMessage("Project is required");
      setOpenAlert(true);
      return;
    }
    if (!event.plannedHours) {
      setMessage("Hours are required");
      setOpenAlert(true);
      return;
    }
    if (!event.product) {
      setMessage("Activity is required");
      setOpenAlert(true);
      return;
    }
    setOpen(false);
    setEvents((events = []) => {
      return [
        ...events,
        {
          ...event,
          color: COLORS[event?.project?.id] || DEFAULT_EVENT_COLOR,
          section_id: `${event.employee && event.employee.id}_${
            event.project && event.project.id
          }`,
          text:
            event?.projectTask?.fullName ||
            event?.project?.fullName ||
            event.text,
        },
      ];
    });
    addProjectLines(event);
  };

  const getDays = React.useCallback((weekends, { date, toDate }) => {
    let startDate = moment(date);
    let endDate = moment(toDate);

    let now = startDate.clone(),
      newDates = [];

    while (now.isSameOrBefore(endDate)) {
      if (!weekends.includes(now.day())) {
        newDates.push(new Date(now));
      }
      now.add(1, "days");
    }
    return newDates;
  }, []);

  const getHalfDays = React.useCallback((weekends, { date, toDate }) => {
    let startDate = moment(date);
    let endDate = moment(toDate);
    let now = startDate.clone(),
      newDates = [];
    while (now.isBefore(endDate)) {
      if (!now.isSame(startDate)) {
        if (!weekends.includes(now.day())) {
          newDates.push(new Date(now));
        }
      }
      now.add(1, "days");
    }
    return newDates;
  }, []);

  const getDaysBetweenDates = React.useCallback(
    (weekends, { date, toDate }, holidays = []) => {
      let startDate = moment(date);
      let endDate = moment(toDate);

      let now = startDate.clone(),
        newDates = [];

      while (now.isSameOrBefore(endDate)) {
        if (
          weekends.includes(now.day()) ||
          holidays.includes(formatDate(now))
        ) {
          newDates.push(new Date(now));
        }
        now.add(1, "days");
      }
      return newDates;
    },
    []
  );

  const getLeaveDays = React.useCallback(
    (empId, weekends) => {
      const leaveDates = (
        leaves?.filter((l) => l?.employee?.id === Number(empId)) || []
      ).map((leave) => {
        return {
          date: leave.fromDateT,
          toDate: leave.toDateT,
          startOnSelect: leave.startOnSelect,
          endOnSelect: leave.endOnSelect,
        };
      });
      const days =
        [
          ...(leaveDates.map((dates) => {
            return getDays(weekends, dates).map((f) => formatDate(f)) || [];
          }) || []),
        ].flat() || [];
      return days;
    },
    [leaves, getDays]
  );

  const getPlannedHours = React.useCallback(
    (
      { start_date, end_date, startOnSelect, endOnSelect },
      hoursPerMode,
      empId
    ) => {
      const weekends = [];
      const plan = weeklyPlanning && weeklyPlanning[empId];
      const holidayPlans = (holidays && holidays[empId]?.holidays) || [];
      for (const [key, value] of Object.entries(plan || {})) {
        if (!Object.values(value).find((s) => s !== "0")) {
          weekends.push(Number(key));
        }
      }
      const days = getLeaveDays(empId, weekends) || [];
      if (mode === "halfday_timeline") {
        const duration =
          Number(hoursPerMode) *
            moment(end_date).diff(moment(start_date), "days") *
            2 -
          getDaysBetweenDates(
            weekends,
            {
              date: start_date,
              toDate: moment(end_date).clone().subtract(1, "days"),
            },
            [...holidayPlans, ...days]
          ).length *
            Number(hoursPerMode) *
            2;
        return (
          duration +
          (startOnSelect === endOnSelect &&
          !weekends.includes(moment(end_date).day()) &&
          !weekends.includes(moment(start_date).day())
            ? Number(hoursPerMode)
            : 0)
        );
      }
      return (
        Number(hoursPerMode) *
          moment(end_date).diff(moment(start_date), TYPE[mode]) -
        (mode === "day_timeline"
          ? getDaysBetweenDates(
              weekends,
              {
                date: start_date,
                toDate: moment(end_date).clone().subtract(1, "days"),
              },
              [...holidayPlans, ...days]
            ).length * Number(hoursPerMode)
          : 0)
      );
    },
    [mode, getDaysBetweenDates, weeklyPlanning, holidays, getLeaveDays]
  );

  const handleEvent = (name, value) => {
    if (["hoursPerMode", "end_date", "start_date"].includes(name)) {
      setEvent((event) => {
        const newEvent = { ...event, [name]: value };
        return {
          ...newEvent,
          color: COLORS[newEvent?.project?.id] || DEFAULT_EVENT_COLOR,
          plannedHours: ["end_date", "start_date"].includes(name)
            ? getPlannedHours(
                newEvent,
                newEvent?.hoursPerMode,
                newEvent?.employee?.id
              )
            : getPlannedHours(newEvent, value, newEvent?.employee?.id),
        };
      });
      return;
    }
    setEvent((event) => {
      return {
        ...event,
        color: COLORS[event?.project?.id] || DEFAULT_EVENT_COLOR,
        [name]: value,
      };
    });
  };

  const formatDate = (date) => {
    return moment(date).format("YYYY-MM-DD");
  };

  const addSpans = React.useCallback(
    (plans) => {
      let sectionIds = [];
      sections.forEach((element) => {
        const { children } = element;
        sectionIds = [...sectionIds, ...children.map((c) => c.key)];
      });
      sectionIds.forEach((id) => {
        const ids = id?.split("_");
        const holidayConfig = {
          days: [...(plans[ids[0]].holidays.map((w) => new Date(w)) || [])],
          css: "holiday_section",
          zones: "fullday",
          type: "dhx_time_block",
          sections: {
            day_timeline: id,
            halfday_timeline: id,
            week_timeline: id,
            month_timeline: id,
          },
        };
        scheduler.deleteMarkedTimespan(holidayConfig);
        scheduler.addMarkedTimespan(holidayConfig);
      });
      scheduler.updateView();
    },
    [sections]
  );

  const addMember = async (record) => {
    await updateProject(record);
  };

  /**
   *
   * @param {*} section
   * If section(employee) is available check for children(projects).
   *  Add new projects
   *    If project is available keep as it is
   *    else add new project and update memberUserSet in project
   * else
   *  Add new employee with projects (Recursive project logic)
   */
  const addNewSection = (section) => {
    const { employee, projects } = section || {};
    const projectIds = projects?.map((p) => p.id);
    const isSection = sections?.find((s) => s.id === employee?.id);
    setEmployees((employees) => [...(employees || []), employee]);
    setProjects((projs) => [...(projs || []), ...projects]);
    if (isSection) {
      const { children } = isSection || {};
      const childrenIds = children.map((c) => c.id);
      const addIds = projectIds.filter((p) => !childrenIds.includes(p));
      setSections((sections = []) =>
        sections.map((section) =>
          section?.id === employee?.id
            ? {
                ...section,
                children: [
                  ...section.children,
                  ...(addIds?.map((id) => {
                    const proj = projects.find((p) => p.id === id);
                    if (
                      !proj.membersUserSet.find(
                        (m) => m.id === employee?.user?.id
                      )
                    ) {
                      addMember({
                        ...proj,
                        membersUserSet: [...proj.membersUserSet, employee.user],
                      });
                      return {
                        ...proj,
                        key: `${employee.id}_${proj.id}`,
                        label: proj.fullName,
                      };
                    }
                    return {
                      ...proj,
                      key: `${employee.id}_${proj.id}`,
                      label: proj.fullName,
                    };
                  }) || []),
                ],
              }
            : { ...section }
        )
      );
    } else {
      setSections((sections) => [
        ...sections,
        {
          ...employee,
          key: employee.id,
          label: employee.name,
          open: true,
          children: [...(projects || [])]?.map((p) => {
            if (
              projects?.find((proj) =>
                (proj.membersUserSet || [])
                  .map((u) => u.id)
                  .includes(employee.user?.id)
              )
            ) {
              return { ...p, key: `${employee.id}_${p.id}`, label: p.fullName };
            } else {
              addMember({
                ...p,
                membersUserSet: [...p.membersUserSet, employee.user],
              });
              return { ...p, key: `${employee.id}_${p.id}`, label: p.fullName };
            }
          }),
        },
      ]);
    }
  };

  useEffect(() => {
    (async () => {
      const { date, toDate } = dates;
      if (!date || !toDate || !employees?.length || !projects?.length) return;
      const res = await Service.search(
        "com.axelor.apps.project.db.ProjectPlanningTime",
        {
          data: {
            criteria: [
              {
                fieldName: "date",
                operator: ">=",
                value: date,
              },
              {
                fieldName: "date",
                operator: "<=",
                value: toDate,
              },
              {
                fieldName: "employee.id",
                operator: "IN",
                value: employees.map((e) => e.id) || [],
              },
              {
                fieldName: "project.id",
                operator: "IN",
                value: projects.map((e) => e.id) || [],
              },
              {
                fieldName: "modeTypeSelect",
                operator: "=",
                value: modeTypeSelect[mode],
              },
            ],
            operator: "and",
          },
        }
      );
      setEvents(mapPlanningTimes(res?.data));
    })();
  }, [employees, projects, mapPlanningTimes, dates, mode]);

  useEffect(() => {
    /**
     * Update timeline size dynamically
     */
    const onBeforeViewChange = scheduler.attachEvent(
      "onBeforeViewChange",
      function (old_mode, old_date, mode, new_date) {
        if (old_mode === mode && formatDate(old_date) === formatDate(new_date))
          return true;
        let date = formatDate(moment(new_date).startOf("month"));
        let toDate = formatDate(moment(new_date).endOf("month"));
        if (mode === "week_timeline") {
          const start = moment(new_date).startOf("week");
          date = formatDate(moment(new_date).startOf("week"));
          toDate = formatDate(start.add(14, "week").endOf("week"));
        } else if (mode === "month_timeline") {
          date = formatDate(moment(new_date).startOf("year"));
          toDate = formatDate(moment(new_date).endOf("year"));
        }
        setDates({
          date,
          toDate,
        });
        setMode(mode);
        LocalStorageService.setActiveMode(mode);
        let day_timeline = scheduler.getView("day_timeline");
        let halfday_timeline = scheduler.getView("halfday_timeline");
        day_timeline.x_size = moment(date).daysInMonth();
        day_timeline.x_length = moment(date).daysInMonth();
        halfday_timeline.x_size = moment(date).daysInMonth() * 2;
        halfday_timeline.x_length = moment(date).daysInMonth() * 2;
        return true;
      }
    );

    const onViewChange = scheduler.attachEvent(
      "onViewChange",
      function (mode, date) {
        if (mode === "month_timeline") {
          scheduler.config.time_step =
            moment(date).daysInMonth() * time_step["day_timeline"];
        } else {
          scheduler.config.time_step = time_step[mode];
        }
      }
    );
    return () => {
      scheduler.detachEvent(onBeforeViewChange);
      scheduler.detachEvent(onViewChange);
    };
  }, []);

  useEffect(() => {
    const onEventChanged = scheduler.attachEvent(
      "onEventChanged",
      function (id) {
        let ev = scheduler.getEvent(id);
        const ids = ev?.section_id?.split("_");
        let project = projects?.find((e) => e.id === Number(ids && ids[1]));
        let employee = employees?.find((e) => e.id === Number(ids && ids[0]));
        const employeeId = employee?.id;
        if (ev?.version >= 0) {
          const startHour = moment(ev.start_date).hour();
          const endHour = moment(ev.end_date).hour();
          addProjectLines({
            ...ev,
            plannedHours: getPlannedHours(
              {
                start_date: ev.start_date,
                end_date: ev.end_date,
                startOnSelect:
                  modeTypeSelect[mode] === 1 ? (startHour >= 12 ? 2 : 1) : 1,
                endOnSelect:
                  modeTypeSelect[mode] === 1 ? (endHour >= 12 ? 1 : 2) : 2,
              },
              ev.hoursPerMode,
              employeeId
            ),
            startOnSelect:
              modeTypeSelect[mode] === 1 ? (startHour >= 12 ? 2 : 1) : 1,
            endOnSelect:
              modeTypeSelect[mode] === 1 ? (endHour >= 12 ? 1 : 2) : 2,
            color: COLORS[ev?.project?.id] || DEFAULT_EVENT_COLOR,
            text: ev?.projectTask?.fullName || project?.fullName || ev.text,
            project,
            employee,
          });
        }
      }
    );

    return () => {
      scheduler.detachEvent(onEventChanged);
    };
  }, [employees, getPlannedHours, projects, mode, addProjectLines]);

  useEffect(() => {
    const openView = (id) => {
      let ev = scheduler.getEvent(id);
      const startHour = moment(ev.start_date).hour();
      const endHour = moment(ev.end_date).hour();
      const ids = ev?.section_id?.split("_");
      const employee = employees?.find((e) => e.id === Number(ids && ids[0]));
      setEvent({
        ...ev,
        color: COLORS[ev.project?.id] || DEFAULT_EVENT_COLOR,
        modeTypeSelect: modeTypeSelect[mode],
        product: ev.product || employee?.product,
        hoursPerMode: ev.hoursPerMode || employee?.dailyWorkHours,
        plannedHours: getPlannedHours(
          {
            start_date: ev.start_date,
            end_date: ev.end_date,
            startOnSelect:
              modeTypeSelect[mode] === 1 ? (startHour >= 12 ? 2 : 1) : 1,
            endOnSelect:
              modeTypeSelect[mode] === 1 ? (endHour >= 12 ? 1 : 2) : 2,
          },
          ev.hoursPerMode || employee?.dailyWorkHours,
          employee?.id
        ),
        text: ev?.projectTask?.fullName || ev?.project?.fullName,
        startOnSelect:
          modeTypeSelect[mode] === 1 ? (startHour >= 12 ? 2 : 1) : 1,
        endOnSelect: modeTypeSelect[mode] === 1 ? (endHour >= 12 ? 1 : 2) : 2,
        project: projects?.find((e) => e.id === Number(ids && ids[1])),
        employee,
      });
      setOpen(true);
    };
    const onClick = scheduler.attachEvent("onClick", function (id, e) {
      openView(id);
    });

    scheduler.showLightbox = (id) => {
      openView(id);
    };

    return () => {
      scheduler.detachEvent(onClick);
    };
  }, [mode, employees, getPlannedHours, projects]);

  useEffect(() => {
    scheduler.plugins({
      timeline: true,
      treetimeline: true,
      limit: true,
      tooltip: true,
    });
    scheduler.locale.labels.day_timeline = translate("Day");
    scheduler.locale.labels.week_timeline = translate("Week");
    scheduler.locale.labels.month_timeline = translate("Month");
    scheduler.locale.labels.halfday_timeline = translate("Half day");
    scheduler.locale.labels.dhx_cal_today_button = translate("Today");
    scheduler.locale.labels.section_custom = translate("Section");

    scheduler.config.details_on_create = true;
    scheduler.config.details_on_dblclick = true;
    scheduler.config.time_step = 1440;
    scheduler.xy.scale_height = 20;
    scheduler.config.hour_date = "%y-%m-%d %H:%i:%s";
    scheduler.config.check_limits = false;

    const commonProperties = {
      section_autoheight: false,
      y_unit: sections,
      folder_dy: 35,
      y_property: "section_id",
      dy: 60,
      render: "tree",
      cell_template: true,
    };
    scheduler.createTimelineView({
      name: "halfday_timeline",
      x_unit: "minute",
      x_date: "%A",
      x_step: 720,
      x_size: 62,
      x_start: 0,
      x_length: 62,
      ...commonProperties,
      second_scale: {
        x_unit: "day",
        x_date: "%D %j",
      },
    });

    scheduler.createTimelineView({
      name: "day_timeline",
      x_unit: "day",
      x_date: "%D %j", //%D<br><b>%j</b>
      days: 1,
      x_step: 1,
      x_start: 1,
      x_size: moment().daysInMonth(),
      x_length: moment().daysInMonth(),
      ...commonProperties,
    });

    scheduler.createTimelineView({
      name: "week_timeline",
      x_unit: "week",
      x_step: 1,
      x_size: 14,
      ...commonProperties,
      second_scale: {
        x_unit: "week",
      },
    });

    scheduler.createTimelineView({
      name: "month_timeline",
      x_unit: "month",
      x_date: "%F",
      x_step: 1,
      x_size: 12,
      ...commonProperties,
    });

    scheduler.templates.week_timeline_second_scale_date = function (date) {
      return `Week ${moment(date).week()}`;
    };

    scheduler.templates.week_timeline_scale_date = function (date) {
      return `${moment(date).startOf("week").format("DD MMM")} - ${moment(date)
        .endOf("week")
        .format("DD MMM")}`;
    };

    //For start date of view
    scheduler.date.halfday_timeline_start = function (date) {
      return new Date(moment(date).startOf("month"));
    };
    scheduler.date.day_timeline_start = function (date) {
      return new Date(moment(date).startOf("month"));
    };
    scheduler.date.week_timeline_start = function (date) {
      return new Date(moment(date).startOf("week"));
    };
    scheduler.date.month_timeline_start = function (date) {
      return new Date(moment(date).startOf("year"));
    };
    scheduler.i18n.setLocale("fr");
    let activeMode = LocalStorageService.getActiveMode() || "day_timeline";
    scheduler.init("scheduler_here", new Date(), activeMode);
  }, [sections, weeklyPlanning, employees]);

  useEffect(() => {
    scheduler.clearAll();
    scheduler.parse(
      events?.filter((e) => e.modeTypeSelect === modeTypeSelect[mode])
    );
    scheduler.updateView();
  }, [mode, events]);

  useEffect(() => {
    const cellValue = function (evs, date, section) {
      if (section.children) {
        const timeline = scheduler.getView();
        const events = timeline.selectEvents({
          section: section.key,
          date: date,
          selectNested: true,
        });
        const startDate = ["day_timeline", "halfday_timeline"].includes(mode)
          ? moment(date)
          : mode === "week_timeline"
          ? moment(date).startOf("week")
          : moment(date).startOf("month");
        const endDate = ["day_timeline", "halfday_timeline"].includes(mode)
          ? moment(date)
          : mode === "week_timeline"
          ? moment(date).endOf("week")
          : moment(date).endOf("month");

        function getTotalHours(empId, startDate, endDate, match = () => true) {
          if (!weeklyPlanning) return 0;
          const emp = weeklyPlanning[empId];
          const holidayPlans = (holidays && holidays[empId]?.holidays) || [];
          const weekends = [];
          for (const [key, value] of Object.entries(emp || {})) {
            if (!Object.values(value).find((s) => s !== "0")) {
              weekends.push(Number(key));
            }
          }
          const leaveDates = getLeaveDays(empId, weekends);
          const getEmpHr = (mm) => {
            const halfs = emp && emp[mm.day()];
            if (!halfs) return 0;
            return +(halfs[0] || 0) + +(halfs[1] || 0);
          };
          if (!(startDate instanceof moment)) {
            startDate = moment(startDate);
          }
          if (!(endDate instanceof moment)) {
            endDate = moment(endDate);
          }
          const $dd = startDate.clone();
          let total = 0;
          while ($dd.isSameOrBefore(endDate)) {
            if (
              match($dd) &&
              !holidayPlans.includes(formatDate($dd)) &&
              !leaveDates.includes(formatDate($dd))
            ) {
              total += getEmpHr($dd);
            }
            $dd.add(1, "days");
          }
          return total;
        }

        function getCount() {
          if (!weeklyPlanning) return 0;
          return events?.reduce((accumulator, object) => {
            return accumulator + (Number(object.hoursPerMode) || 0);
          }, 0);
        }

        const availableHrs = getTotalHours(section.id, startDate, endDate);
        const workedHrs = getCount();
        const percentage =
          Math.round(
            (workedHrs * 100) /
              (mode === "halfday_timeline" ? availableHrs / 2 : availableHrs)
          ) || 0; //Temporary static fix for total hours for half day
        const percent = isFinite(percentage) ? percentage : 0;
        let className = "load-marker-blue";
        if (percent > 100) {
          className = "load-marker-red";
        } else if (percent === 100) {
          className = "load-marker-green";
        }
        return `<div class='load-marker ${className}' title='${percent}%'>${percent}%</div>`;
      }
      return "";
    };
    scheduler.templates.day_timeline_cell_value = cellValue;
    scheduler.templates.week_timeline_cell_value = cellValue;
    scheduler.templates.month_timeline_cell_value = cellValue;
    scheduler.templates.halfday_timeline_cell_value = cellValue;
  }, [mode, weeklyPlanning, holidays, getLeaveDays]);

  useEffect(() => {
    (async () => {
      const employeeIds = employees?.map((emp) => emp.id);
      if (!employeeIds?.length) return;
      const res = await Service.action({
        action: "com.axelor.apps.hr.web.EmployeeController:getDayPlanningHours",
        data: {
          employeeIds,
        },
      });
      setWeeklyPlanning(res?.data);
    })();
  }, [employees]);

  useEffect(() => {
    (async () => {
      const employeeIds = employees?.map((emp) => emp.id);
      if (employeeIds?.length) {
        const { date, toDate } = dates;
        const res = await Service.search("com.axelor.apps.hr.db.LeaveRequest", {
          operator: "and",
          criteria: [
            {
              operator: "or",
              criteria: [
                {
                  operator: "and",
                  criteria: [
                    {
                      fieldName: "fromDateT",
                      fieldType: "date",
                      value: date,
                      operator: ">=",
                    },
                    {
                      fieldName: "fromDateT",
                      fieldType: "date",
                      value: toDate,
                      operator: "<=",
                    },
                  ],
                },
                {
                  operator: "and",
                  criteria: [
                    {
                      fieldName: "toDateT",
                      fieldType: "date",
                      value: date,
                      operator: ">=",
                    },
                    {
                      fieldName: "toDateT",
                      fieldType: "date",
                      value: toDate,
                      operator: "<=",
                    },
                  ],
                },
                {
                  fieldName: "employee",
                  operator: "IN",
                  value: employeeIds,
                },
              ],
            },
          ],
        });
        const leaves = res?.data;
        setLeaves(leaves);
        let sectionIds = [];
        sections.forEach((element) => {
          const { children } = element;
          sectionIds = [...sectionIds, ...children.map((c) => c.key)];
        });
        sectionIds.forEach((id) => {
          const ids = id?.split("_");
          const empId = ids && ids[0];
          const weekends = [];
          const plan = weeklyPlanning && weeklyPlanning[empId];
          for (const [key, value] of Object.entries(plan || {})) {
            if (!Object.values(value).find((s) => s !== "0")) {
              weekends.push(Number(key));
            }
          }
          const leaveDates = (
            leaves?.filter((l) => l?.employee?.id === Number(empId)) || []
          ).map((leave) => {
            return {
              date: leave.fromDateT,
              toDate: leave.toDateT,
              startOnSelect: leave.startOnSelect,
              endOnSelect: leave.endOnSelect,
            };
          });
          const days = [
            ...(leaveDates.map((dates) => {
              return (
                getDays(mode === "day_timeline" ? weekends : [], dates) || []
              );
            }) || []),
          ];
          const config = {
            days: [...days],
            css: "leave_day_section",
            zones: "fullday",
            type: "dhx_time_block",
            sections: {
              day_timeline: id,
              week_timeline: id,
              month_timeline: id,
            },
          };
          scheduler.deleteMarkedTimespan(config);
          scheduler.addMarkedTimespan(config);

          /**
           * Halfday leaves
           */
          const halfdays = [
            ...(leaveDates.map((dates) => {
              return getHalfDays(weekends, dates) || [];
            }) || []),
          ];
          const config3 = {
            days: [...halfdays],
            css: "leave_day_section",
            zones: "fullday",
            type: "dhx_time_block",
            sections: {
              halfday_timeline: id,
            },
          };
          scheduler.deleteMarkedTimespan(config3);
          scheduler.addMarkedTimespan(config3);
          leaveDates.forEach((leave) => {
            if (!weekends.includes(moment(leave.date).day())) {
              const config1 = {
                days: [new Date(leave.date)],
                css: "leave_day_section",
                zones:
                  leave.startOnSelect === 1
                    ? [0, 12 * 60, 12 * 60, 24 * 60]
                    : [12 * 60, 24 * 60],
                type: "dhx_time_block",
                sections: {
                  halfday_timeline: id,
                },
              };

              scheduler.deleteMarkedTimespan(config1);
              scheduler.addMarkedTimespan(config1);
            }
            if (!weekends.includes(moment(leave.toDate).day())) {
              const config5 = {
                days: [new Date(leave.toDate)],
                css: "leave_day_section",
                zones:
                  leave.endOnSelect === 1
                    ? [0, 12 * 60]
                    : [0, 12 * 60, 12 * 60, 24 * 60],
                type: "dhx_time_block",
                sections: {
                  halfday_timeline: id,
                },
              };
              scheduler.deleteMarkedTimespan(config5);
              scheduler.addMarkedTimespan(config5);
            }
          });
        });
        scheduler.updateView();
      }
      /**
       * Non working highlights
       */
      if (!weeklyPlanning) return;
      let sectionIds = [];
      sections.forEach((element) => {
        const { children } = element;
        sectionIds = [...sectionIds, ...children.map((c) => c.key)];
      });
      sectionIds.forEach((id) => {
        const ids = id?.split("_");
        const employeeId = ids[0];
        const plan = weeklyPlanning[employeeId];
        const weekends = [];
        for (const [key, value] of Object.entries(plan || {})) {
          if (!Object.values(value).find((s) => s !== "0")) {
            weekends.push(Number(key));
          }
        }
        const days = getDaysBetweenDates(weekends, dates) || [];
        const config = {
          days: [...days],
          css: "non_working_day_section",
          zones: "fullday",
          type: "dhx_time_block",
          sections: {
            day_timeline: id,
            halfday_timeline: id,
          },
        };
        scheduler.deleteMarkedTimespan(config);
        scheduler.addMarkedTimespan(config);
        scheduler.updateView();
      });
    })();
  }, [
    employees,
    getDays,
    sections,
    mode,
    dates,
    getHalfDays,
    getDaysBetweenDates,
    weeklyPlanning,
  ]);

  useEffect(() => {
    (async () => {
      const employeeIds = employees?.map((emp) => emp.id);
      if (!employeeIds?.length) return;
      const res = await Service.action({
        action:
          "com.axelor.apps.hr.web.EmployeeController:getHolidayForEmployees",
        data: {
          employeeIds,
          fromDate: dates?.date,
          toDate: dates?.toDate,
        },
      });
      setHolidays(res?.data);
      addSpans(res?.data);
    })();
  }, [employees, addSpans, dates]);

  useEffect(() => {
    (async () => {
      if (!id) return;
      const planning = await Service.search(
        "com.axelor.apps.project.db.ProjectPlanning",
        {
          fields: ["employeeSet"],
          data: {
            criteria: [
              {
                fieldName: "id",
                operator: "=",
                value: id,
              },
            ],
          },
        }
      );
      const employeeIds =
        planning?.data && planning.data[0]?.employeeSet?.map((u) => u.id);
      const res = await Service.search("com.axelor.apps.hr.db.Employee", {
        fields: ["fullName", "name", "user", "dailyWorkHours", "product"],
        data: {
          criteria: [
            {
              fieldName: "id",
              operator: "IN",
              value: employeeIds,
            },
          ],
        },
      });
      const employees = res?.data;
      const userIds = employees?.map((emp) => emp?.user?.id);
      setEmployees(employees);
      const projects = await fetchProjects({
        fields: ["name", "membersUserSet", "fullName"],
        data: {
          criteria: [
            {
              fieldName: "membersUserSet.id",
              operator: "IN",
              value: userIds,
            },
            {
              fieldName: "projectStatus.isCompleted",
              operator: "=",
              value: false,
            },
          ],
          operator: "and",
        },
      });
      setProjects(projects);
      const sections = employees?.map((emp) => {
        return {
          ...emp,
          key: emp.id,
          label: emp.name,
          open: true,
          children: [
            ...((projects || []).filter((proj) =>
              (proj.membersUserSet || [])
                .map((u) => u.id)
                .includes(emp.user?.id)
            ) || []),
          ]?.map((p) => {
            return { ...p, key: `${emp.id}_${p.id}`, label: p.fullName };
          }),
        };
      });
      if (!sections) return;
      setSections(sections);
    })();
  }, [id]);

  return (
    <div style={{ width: "100%", height: "100%" }}>
      <Toolbar
        addSection={(section) => {
          addNewSection(section);
        }}
      />
      <>
        <EventFrom
          employees={employees}
          open={open}
          handleEvent={handleEvent}
          event={event}
          handleClose={handleClose}
          handleOk={handleOk}
          handleDelete={handleDelete}
          mode={LABEL[mode]}
        />
        <div id="scheduler_here" className="dhx_cal_container">
          <div className="dhx_cal_navline">
            <div className="dhx_cal_prev_button"></div>
            <div className="dhx_cal_next_button"></div>
            <div className="dhx_cal_today_button"></div>
            <div className="dhx_cal_date"></div>
            <div className="dhx_cal_tab" data-tab="halfday_timeline"></div>
            <div className="dhx_cal_tab" data-tab="day_timeline"></div>
            <div className="dhx_cal_tab" data-tab="week_timeline"></div>
            <div className="dhx_cal_tab" data-tab="month_timeline"></div>
          </div>
          <div className="dhx_cal_header"></div>
          <div className="dhx_cal_data"></div>
        </div>
      </>
      <AlertBar
        open={openAlert}
        message={message}
        handleAlertClose={() => {
          setOpenAlert(false);
          setMessage("");
        }}
      />
    </div>
  );
}
