function isEmpty(obj) {
  for (var prop in obj) {
    return false;
  }
  return true;
}
class LocalStorageService {
  static LAST_ACTIVE_MODE = "EMP_LAST_ACTIVE_MODE";
  static LAST_ACTIVE_DATE = "EMP_LAST_ACTIVE_DATE";

  static setActiveMode(data = null) {
    window.localStorage.setItem(
      LocalStorageService.LAST_ACTIVE_MODE,
      JSON.stringify(data)
    );
  }

  static getActiveMode() {
    try {
      const data = window.localStorage.getItem(
        LocalStorageService.LAST_ACTIVE_MODE
      );
      let jsonData = {};
      if (data) {
        jsonData = JSON.parse(data);
      }
      if (isEmpty(jsonData)) {
        return null;
      }
      return jsonData;
    } catch (e) {}
    return null;
  }

  static setActiveDate(data = null) {
    window.localStorage.setItem(
      LocalStorageService.LAST_ACTIVE_DATE,
      JSON.stringify(data)
    );
  }

  static getActiveDate() {
    try {
      const data = window.localStorage.getItem(
        LocalStorageService.LAST_ACTIVE_DATE
      );
      let jsonData = {};
      if (data) {
        jsonData = JSON.parse(data);
      }

      if (isEmpty(jsonData)) {
        return null;
      }
      return jsonData;
    } catch (e) {}
    return null;
  }
}

export default LocalStorageService;
